import React from "react"
import {Link} from "gatsby"
import styles from "./toolbar.css"
import wikibutton from "../images/wikibutton.png"
import homebrewbutton from "../images/homebrewbutton.png"
import newsletterbutton from "../images/newsletterbutton.png"
import menubutton from "../images/menubutton.png"

const ListLink = props => (
    <li style={{ display: `inline-block`}}>
        <Link to={props.to}>
            <img src={props.iconpath} className={styles.iconimg} style={{left: props.left}} alt=""/>
        </Link>
    </li>
)
export default ({ children }) => (
    <div className={styles.bar}>
        <ul>
            <ListLink to="/" iconpath={wikibutton} left="16"></ListLink>
            <ListLink to="/homebrew/" iconpath={homebrewbutton} left="102"></ListLink>
            <ListLink to="/newsletter/" iconpath={newsletterbutton} left="188"></ListLink>
            <ListLink to="/menu/" iconpath={menubutton} left="274"></ListLink>
        </ul>
        {children}
    </div>

)